//screens
$min-screen : 400px;
$max-screen : 1200px;

//font-size-mobile
$h1-mobile : 28px;
$h2-mobile : 24px;
$h3-mobile : 22px;
$h4-mobile : 20px;
$p-mobile : 16px;
$small-mobile : 14px;

// screen-size-screen
$h1-screen : 39px;
$h2-screen : 31px;
$h3-screen : 25px;
$h4-screen : 20px;
$p-screen : 16px;
$small-screen : 14px;
//-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
h1 {
  @include mainFont;
  //font-size   : rem(39px);
  @include fluid-type($min-screen, $max-screen, $h1-mobile, $h1-screen);
  font-weight : 400;
  line-height : 120%;
  text-align  : center;
  color       : $light;

  strong, b {
    font-weight : 400;
    color       : $color-5;
  }
  .header & {
    text-shadow : rgba(0, 0, 0, 1) 0 2px 2px;
  }
}
h2 {
  @include fluid-type($min-screen, $max-screen, $h2-mobile, $h2-screen);
  line-height   : 140%;
  font-family   : $main-font-family;
  color         : $color-2;
  font-weight   : 500;
  text-align    : center;
  margin-top    : rem(80px);
  margin-bottom : rem(130px);
}
h3 {
  @include fluid-type($min-screen, $max-screen, $h3-mobile, $h3-screen);
  line-height   : 140%;
  font-family   : $main-font-family;
  color         : $color-2;
  font-weight   : 500;
  text-align    : center;
  margin-top    : rem(80px);
  margin-bottom : rem(130px);
}
h4 {
  @include fluid-type($min-screen, $max-screen, $h4-mobile, $h4-screen);
  line-height   : 140%;
  font-family   : $main-font-family;
  color         : $color-2;
  font-weight   : 500;
  text-align    : center;
  margin-top    : rem(80px);
  margin-bottom : rem(130px);
}
h4 {
  @include fluid-type($min-screen, $max-screen, $h4-mobile, $h4-screen);
  line-height   : 140%;
  font-family   : $main-font-family;
  color         : $color-2;
  font-weight   : 500;
  text-align    : center;
  margin-top    : rem(80px);
  margin-bottom : rem(130px);
}
//-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
p {
  @include fluid-type($min-screen, $max-screen, $p-mobile, $p-screen);
  font-family : $title-font-family;
  line-height : 140%;
  font-weight : 500;
}
//-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
.callToActionPhone {
  font-family : $main-font-family;
  font-size   : rem(25px);
  line-height : rem(33px);
  font-weight : 300;

  strong, b {
    font-weight : 500;
  }
}
//-------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------
